table {
  color: @navy;
  width: 100%;
  & tr {
    &:nth-child(2n) {
      background: #fafafa;
    }
    &:nth-child(2n+1) {
      background: #f2f0f0;
    }
  }
  & th {
    background: #00a2dd;
    font-size: 13px;
    color: white;
    line-height: 20px;
    font-weight: 500;
    text-align: center;
    padding: 15px 20px;
    &:first-child {
      text-align: left;
      width: 120px;
    }
  }
  & td {
    font-size: 13px;
    padding: 15px 0;
    line-height: 20px;
    font-weight: 300;
    text-align: center;
    vertical-align: text-bottom;
    border-right: none;
    &:first-child {
      text-align: left;
      padding-left: 23px;
    }
    & a {
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.table-container {
  max-width: 100%;
  overflow-x: auto;
  &::-webkit-scrollbar {
    height: 4px;
  }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, .2);

  }
  &::-webkit-scrollbar-thumb {
    outline: 1px solid @navy;
    background-color: @navy;
    border-radius: 5px;
  }
  & table {
    min-width: 600px;
  }
}

.mobile-menu .wrapper .menu-content
{
  overflow-y: auto;
  height: 80vh;

  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.18);
    background: #42b6e0;
  }
  &::-webkit-scrollbar-thumb {
    outline: 1px solid #fff;
    background-color:  #fff;
    border-radius: 5px;
  }
}


#chart {
  width: 650px;
  height: 300;
  margin: 30px auto 0;
  display: block;
}

#chart #numbers {
  height: 100%;
  width: 50px;
  margin: 0;
  padding: 0;
  display: inline-block;
  float: left;
}

#chart #numbers li {
  text-align: right;
  padding-right: 1em;
  list-style: none;
  height: 29px;
  border-bottom: 1px solid #444;
  position: relative;
  bottom: 30px;
}

#chart #numbers li:last-child { height: 30px; }

#chart #numbers li span {
  color: #eee;
  position: absolute;
  bottom: 0;
  right: 10px;
}

#chart #bars {
  display: inline-block;
  background: rgba(0, 0, 0, 0.2);
  width: 600px;
  height: 300px;
  padding: 0;
  margin: 0;
  box-shadow: 0 0 0 1px #444;
}

#chart #bars li {
  display: table-cell;
  width: 100px;
  height: 300px;
  margin: 0;
  text-align: center;
  position: relative;
}

#chart #bars li .bar {
  display: block;
  width: 70px;
  margin-left: 15px;
  background: #49E;
  position: absolute;
  bottom: 0;
}

#chart #bars li .bar:hover {
  background: #5AE;
  cursor: pointer;
}

#chart #bars li .bar:hover:before {
  color: white;
  content: attr(data-percentage) '%';
  position: relative;
  bottom: 20px;
}

#chart #bars li span {
  color: #eee;
  width: 100%;
  position: absolute;
  bottom: -2em;
  left: 0;
  text-align: center;
}