.btn {
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 12px;
  font-weight: 300;
  background-repeat: no-repeat;
  padding: 8px 12px;
  line-height: 1.2em;
  border: 1px solid @navy;
  border-radius: 50px;
  font-family: @title-font;
  vertical-align: middle;
  color: @navy;
  transition: 0.4s;
  background: transparent;
  &.long-btn {
    border-radius: 50px;
    padding: 11px 28px 9px;
  }
  &.order-btn {
    padding: 11px 18px 9px;
    padding-left: 46px;
    white-space: nowrap;
    top: -9px;
    position: relative;
    &:before {
      content: url(../img/cart-w.png);
      position: absolute;
      left: 14px;
      top: 8px;
    }
  }
  &:hover {
    opacity: 0.9;
  }
  &.sml-btn {
    font-size: 15px;
    font-weight: 400;
    padding: 7px 14px 7px;
    margin-right: 3px;
  }
}

.site-btn {
  background: rgba(255, 255, 255, 0.39);
  border: none;
  color: white;
  padding: 7px 15px;
}

.subscribe-button {
  background: @cyan;
  border: none;
  color: white;
}

.blue-btn {
  background: @cyan;
  border: none;
  color: white;
  &.dark {
    background: @blue;
  }
  &.like-btn, &.dislike-btn {
    font-weight: 600;
    padding: 8px 22px;
    font-size: 13px;
    margin-bottom: 5px;
    &.dark {
      background: @navy;
    }
  }
}

.myth-btn {
  font-size: 13px;
  font-weight: 600;
  padding: 7px 21px;
  background: @navy;
  color: white;
}

& .more-btn {
  background: @blue;
  position: relative;
  padding: 7px 12px;
  padding-right: 47px;
  border: none;
  color: white;
  &:after {
    position: absolute;
    content: url("../img/arr-w.png");
    right: 13px;
    top: 7px;
  }
  &:hover {
    background: @red;
  }
}

&.job-btn {
  padding: 7px 10px;
  padding-right: 42px;
  border: none;
  background: @light-blue;
  font-weight: 600;
  color: white;
  font-size: 13px;
  position: relative;
  top: -6px;
  background-image: url(../img/mai-wl.png);
  background-repeat: no-repeat;
  background-position: 90%;
}

&.trans-btn {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.53);
  color: white;
  font-size: 12px;
  font-weight: 300;
  padding: 8px 12px;
}

.form__btn {
  font-size: 12px;
  color: white;
  background: @blue;
  border: none;
  position: relative;
  top: -7px;
  padding: 7px 14px;
  float: right;
}

&.disabled {
  background: #f1f3f4;
  color: #a0a7b9;
  border: 1px solid #dce4e8;
}

.app-btn {
  font-size: 14px;
  color: white;
  background: @blue;
  position: relative;
  padding: 12px 17px;
  padding-left: 45px;
  background-repeat: no-repeat;
  border-radius: 5px;
  border: none;
  &.ios-btn {
    background-image: url("../img/ios-icon.png");
    background-position: 13%;
  }
  &.and-btn {
    background-image: url("../img/android-icon.png");
    background-position: 13%;
  }
}

.arr-btn {
  font-size: 16px;
  color: white;
  padding: 12px 20px;
  padding-right: 65px;
  position: relative;
  font-weight: 500;
  &:after {
    position: absolute;
    content: url("../img/arr-w.png");
    right: 20px;
    top: 10px;
  }
}

.btn-modal {
  font-size: 12px;
  color: white;
  background: @blue;
  position: relative;
  font-weight: 300;
  border: none;
}

.buy-btn {
  background: #00bfa5;
  color: #fff !important;
  font-size: 13px !important;
  padding-right: 58px;
}