.spez__left {
  float: left;
}
.spez__wrap {
  display: inline-block;
  vertical-align: middle;
  height: 32px;
  line-height: 32px;
}
.spez__color_text {
  font-size: 15px!important;
  font-weight: 700;
  color: #000 !important;
  margin-right: 10px;
  margin-left: 15px;
  display: inline-block;
  vertical-align: middle;
}
.spez__colors {
  display: inline-block;
  vertical-align: middle;
  font-size: 25px;
}
.spez__color {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  background: white;
  border: 2px solid #000000;
  border-radius: 7px;
  width: 40px;
  height: 40px;
  font-weight: 700;
  text-align: center;
  color: #000 !important;
  line-height: 35px;
  box-sizing: border-box;
  font-size: 25px!important;
  margin-right: 3px;
}
.spez__color:hover {
  text-decoration: underline;
}
.spez__color[data-color="black"] {
  background: black;
  color: white !important;
  border-color: black;
}
.spez__color.m--current:after {
  position: absolute;
  content: "";
  width: 5px;
  height: 5px;
  bottom: -10px;
  border-radius: 50%;
  left: 50%;
  margin-left: -2px;
  background: #000;
}
.spez__scale_text {
  padding-left: 40px;
  font-size: 15px !important;
  line-height: 39px;
  font-weight: bold;
  color: #000 !important;
  margin-left: 162px;
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
}
.spez__scales {
  display: inline-block;
  vertical-align: baseline;
  margin-right: 22px;
  line-height: 20px;
}
.spez__scale {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  font-weight: bold;
  color: #000 !important;
  line-height: 1em;
  width: 40px;
  height: 40px;
  border: 2px solid transparent;
  line-height: 36px;
  text-align: center;
}
.spez__scale:hover {
  text-decoration: underline;
}
.spez__scale.m--current {
  border: 2px solid #000000;
  border-radius: 7px;
  width: 40px;
  height: 40px;
  line-height: 36px;
  text-align: center;
  background: #fff;
}
.spez__scale.m--current:after {
  position: absolute;
  content: "";
  width: 5px;
  height: 5px;
  bottom: -10px;
  border-radius: 50%;
  left: 50%;
  margin-left: -2px;
  background: #000;
}
.spez__scale[data-scale="1"] {
  font-size: 20px !important;
}
.spez__scale[data-scale="2"] {
  font-size: 25px !important;
}
.spez__scale[data-scale="3"] {
  font-size: 30px !important;
}
