      margin-bottom: 33px;
      margin-right: 0;
    }
  }

  .main-new .news__item {
    position: relative;
    margin-bottom: 40px !important;
  }

  .youth__list .item .img-container img {
    &:not(.gerb) {
      width: 100%;
    }
  }

  .app-block {
    text-align: center;
    & .img-container .qr {
      position: initial;
      width: 142px;
      margin-top: 20px;
    }
    & .app-btn {
      &.ios-btn {
        margin-left: 0;
        margin-right: 23px;
      }
      &.and-btn {
        margin-right: 0;
        float: initial;
      }
    }
  }

  .more-myth .item .descr p {
    margin-top: 0;
  }

  .page-top .bots {
    position: initial;
    margin-top: 20px;
    & a {
      margin-left: 0;
      margin-right: 10px;
    }
  }

  .team__list .item .img-container {
    img {
      width: 100%;
    }
  }

  .map-container {
    transform: scale(.55);
    left: -139px;
    top: -49px;
  }

  .regions__map-scroll {
    max-width: 100%;
    //min-height: 491px;
    overflow-x: scroll;
    min-height: 586px;
  }

  .regions__map-container {
    transform: scale(1);
    left: 0;
    top: -49px;
  }

  .map-col {
    height: 286px;
  }

  .main-tabs {
    & img.tab-bg {
      width: 100%;
      position: absolute;
      z-index: -1;
      left: 0;
      top: 0;
    }
    & .tab-text {
      position: initial;
      margin: 16px 0;
    }
    & .tab {
      height: auto;
      margin-bottom: 20px;
      & > a {
        display: block;
        position: relative;
        padding: 20px;
        overflow: hidden;
        border-radius: 10px;
      }
      & .trans-btn {
        position: initial;
      }
      & .icon {
        position: initial;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .spez__scales {
    margin-right: 0;
  }

  .spez__back,
  .spez__scale_text {
    padding-left: 0;
  }

  .spez__color_text {
    margin-left: 0;
  }
}

@media screen and (max-width: 425px) {
  .contact-col .btns .btn {
    width: 100%;
    margin-top: 15px;
  }

  .siluet {
    &-li {
      display: block !important;
      float: initial !important;
    }
    &:before {
      left: 0;
    }
  }

}

@media screen and (max-width: 400px) {
  .map-container {
    transform: scale(.45);
    left: -200px;
    top: -85px;
  }

  .map-col {
    height: 236px;
  }
}

